
.c-cuadro {
    &__sup {
        background-color: #ffc1007d;
        z-index: 99;
        position: fixed;
        top: 0.938rem;
        left: .9375rem;
        height: 1.25rem;
        width: calc( 100% - 1.875rem );
        &--mod {
            backdrop-filter: blur(2px);
            height: 2.1875rem;
            top: 0;
            left: 0;
            width: 100%;
        }
    }
    &__der {
        background-color: #ffc1007d;
        z-index: 99;
        position: fixed;
        top: 2.1875rem;
        right: .9375rem;
        width: 1.25rem;
        height: calc( 100% - 4.375rem );
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        &--mod {
            backdrop-filter: blur(1px);
            height: calc( 100% - 3.4375rem );
            right: 0;
        }
        
        a {
            font-size: .375em;
            margin: 0.2rem 0;
        }
    }
    &__inf {
        background-color: #ffc1007d;
        z-index: 99;
        position: fixed;
        left: .9375rem;
        bottom: .9375rem;
        height: 1.25rem;
        width: calc( 100% - 1.875rem );
        display: flex;
        justify-content: center;
        align-items: center;
        &--mod {
            backdrop-filter: blur(1px);
            left: 0;
            bottom: 0;
            width: 100%;
        }
        p {
            font-size: .9375rem;
            font-style: italic;
        }
        // :nth-child(2){
        //     display: none;
        // }
        span {
            margin: 0 .625rem;
        }
        a {
            color: #fff;
        }
    }
    &__izq {
        background-color: #ffc1007d;
        z-index: 99;
        position: fixed;
        top: 2.1875rem;
        left: .9375rem;
        width: 1.25rem;
        height: calc( 100% - 4.375rem );
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        &--mod {
            backdrop-filter: blur(1px);
            height: calc( 100% - 3.4375rem );
            left: 0;
        }
        a {
            margin: 0.5rem 0;
        }
    }
}